import React, { Component } from "react";
//import { CSSTransitionGroup } from 'react-transition-group'
import Autosuggest from 'react-autosuggest';
import { HERONAMES } from './Constants'
import { NavLink } from 'react-router-dom'
import { checkContainsInList } from './Utils'
import { withRouter } from 'react-router'

var isMobile = require('ismobilejs');

const getSuggestions = value => {
  const inputValue = value.trim().toLowerCase();
  const inputLength = inputValue.length;
  const maxReturned = 10;
  return inputLength === 0 ? [] : (HERONAMES.filter(hero =>
    hero.toLowerCase().slice(0, inputLength) === inputValue).slice(0, maxReturned)
  );
};

const getSuggestionValue = suggestion => suggestion;

class SearchBar extends Component {
  constructor(props) {
    super(props);

    this.state = {
      value: '',
      suggestions: [],
    };
  }

  onChange = (event, { newValue }) => {
    this.setState({
      value : newValue
    });
  };

  onSuggestionsFetchRequested = ({ value }) => {
    this.setState({
      suggestions: getSuggestions(value)
    });
  };

  onSuggestionsClearRequested = () => {
    this.setState({
      suggestions: []
    });
  };

  renderSuggestion = suggestion => (
    suggestion ? 
    // <CSSTransitionGroup
    //   transitionName="suggestion"
    //   transitionAppear={true}
    //   transitionAppearTimeout={500}
    //   transitionEnterTimeout={500}
    //   transitionLeaveTimeout={300}>
      <div>
        <NavLink className = 'suggestion-link' 
          activeClassName="suggestion-link-selected" 
          key = {'search:' + suggestion} 
          to = {'/hero/'+ suggestion} 
          onClick = {() => this.onClickChange(suggestion)} >
            <span>
            {suggestion}
            </span>
        </NavLink>
      </div> : null
    // </CSSTransitionGroup> : null
  );

  onClickChange = (suggestion) => {
    this.setState({
      value : suggestion
    }, () => this.updateHero());
  }

  updateHero = () => {
    if (checkContainsInList(HERONAMES, this.state.value)) {
      this.props.history.push('/hero/' + this.state.value)
      this.props.history.go()
    }
  }

  handleKeyPress = (event) => {
    if(event.key === 'Enter'){
      this.updateHero()
    }
  }

  render() {
    const { value, suggestions } = this.state;

    const inputProps = {
      placeholder: 'Search...',
      value,
      onChange: this.onChange,
      onKeyPress: this.handleKeyPress,
    };

    return (
      <div className='search'>
      <Autosuggest
        focusInputOnSuggestionClick={!isMobile}
        suggestions={suggestions}
        onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
        onSuggestionsClearRequested={this.onSuggestionsClearRequested}
        getSuggestionValue={getSuggestionValue}
        renderSuggestion={this.renderSuggestion}
        inputProps={inputProps}
      />
      </div>
    );
  }
}

export default withRouter(SearchBar)


